.article-details-module{
  background-color: #fff;
  height: 100%;
  overflow-y: auto;
}
.article-details{
  font-size: 17px;
  margin: 0 18px;
  overflow: hidden;
  padding-bottom: 10px;
}
.article-details .article-details-title{
  font-size: 18px;
  line-height: 1.5;
  margin-top: 10px;
}
.article-details .article-details-content{
  text-align: justify;
}
.article-details .article-time{
  color: #999;
  text-align: right;
}



