.etp-info{
  display: -ms-flexbox;
  display: flex;
  margin-top: 8px;
  overflow: hidden;
}
.etp-info > img{
  margin-right: 8px;
}
.etp-info .info{
  -ms-flex: 1 1;
      flex: 1 1;
  overflow: hidden;
}
.etp-info .info .etpName{
  font-size: 16px;
  line-height: 1;
}
.etp-info .info .title{
  vertical-align: middle;
}
.etp-info .info .etpArea{
  color: #999;
  font-size: 14px;
  line-height: 2.2;
}
.etp-info .info .vip{
  background: url(images/etp-vip.png) no-repeat 0 0;
  background-size: 36px 18px;
  display: inline-block;
  height: 18px;
  margin-left: 4px;
  vertical-align: middle;
  width: 36px;
}



