.company-details-module {
  background-color: #fff;
  height: 100%;
  overflow-y: auto;
}
.company-details-module .company-top-intro {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  margin: 15px;
}
.company-details-module .company-top-intro > img {
  -ms-flex-item-align: start;
      align-self: flex-start;
  border: 1px solid #e5e5e5;
  border-radius: 3px;
  margin-right: 15px;
}
.company-details-module .company-top-intro .intro-title {
  color: #333;
  -ms-flex: 1 1;
      flex: 1 1;
  line-height: 1.2;
}
.company-details-module .company-top-intro .title-attention {
  color: #666;
  margin-top: 4px;
}
.company-details-module .company-top-intro .btn {
  background-color: #95bb4a;
  border-radius: 3px;
  color: #fff;
  font-size: 15px;
  height: 28px;
  line-height: 28px;
  text-align: center;
  width: 58px;
}
.company-details {
  background-color: #f2f2f2;
  overflow: hidden;
}
.company-details .tab-list {
  margin-top: 10px;
  position: relative;
}
.company-details .tab-list .weui-navbar {
  background-color: #fff;
}
.weui-tab .weui-navbar__item {
  padding: 0;
}
.weui-tab .weui-navbar__item::after {
  display: none;
}
.weui-tab .weui-bar__item_on {
  background-color: inherit;
  color: #95bb4a;
}
.company-details .tab-list .nav-title {
  display: inline-block;
  padding: 12px 0;
}
.weui-tab .weui-bar__item_on .nav-title {
  border-bottom: 2px solid #95bb4a;
  font-size: 16px;
}
.company-details .tab-list .details-summary {
  background-color: #fff;
  padding: 12px 15px;
}
.details-summary .summary-intro {
  border-bottom: 1px solid #e6e6e6;
  color: #999;
  padding-bottom: 10px;
}
.details-summary .summary-intro .intro{
  display: -ms-flexbox;
  display: flex;
}
.details-summary .summary-intro span {
  color: #333;
  -ms-flex: 1 1;
      flex: 1 1;
}
.company-details .tab-list .content-sum{
  margin-top: 15px;
}
.tab-list .joblist{
  background-color: #fff;
  padding: 15px;
}
.tab-list .joblist .position{
  font-size: 16px;
  margin-bottom: 15px;
}
.tab-list .joblist .position-sub{
  font-size: 14px;
}
/* 对话hr */
.hr{
  background-color: #fff;
  padding: 15px;
}
.hr .hr-intro{
  border-bottom: 1px solid #eee;
  font-size: 14px;
  padding-bottom: 10px;
}
.weui-btn_primary{
  background-color: #95bb4a;
}
.weui-btn_primary:not(.weui-btn_disabled):active{
  background-color: #95bb4a;
}
.weui-btn:after{
  display: none;
}
.weui-textarea{
  font-size: 14px;
  padding: 10px 0;
}



