.global-header{
  height: 46px;
  position: relative;
  z-index: 501;
}
.global-header .header{
  background-color: #fff;
  border-bottom: 1px solid #e6e6e6;
  left: 0;
  padding: 0 18px;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 100;
}
.global-header .header > img{
  float: left;
  margin-top: 11px;
}
.global-header .header .headerName{
  color: #333;
  font-size: 18px;
  height: 44px;
  line-height: 44px;
  text-align: center;
}



