.job-details-module {
  background-color: #fff;
  height: 100%;
  overflow-y: auto;
}
.job-details {
  background-color: #f2f2f2;
}
.job-details-info {
  background-color: #fff;
  overflow: hidden;
  padding-top: 10px;
}
.job-details-info .job-info {
  border-bottom: 1px solid #eee;
  padding: 0 15px;
}
.job-details-info .job-info .job-title {
  font-size: 16px;
}
.job-info .job-title .job-name {
  display: block;
  overflow: hidden;
}
.job-info .job-title .job-salary {
  color: #ff7452;
  float: right;
}
.job-info-sub {
  color: #666;
  font-size: 12px;
  margin: 8px 0;
}
.job-info .job-info-sub .info-sub {
  display: inline-block;
  line-height: 1.5;
  padding-right: 15px;
  vertical-align: top;
}
.job-info-sub .info-sub > i {
  display: inline-block;
  height: 16px;
  vertical-align: middle;
  width: 22px;
}
.job-info-sub .info-sub .location {
  background: url(images/location.png) no-repeat 0 0;
  background-size: 12px 14px;
}
.job-info-sub .info-sub .recruit {
  background: url(images/recruit-number.png) no-repeat 0 0;
  background-size: 16px 14px;
}
.job-info-sub .info-sub .jobType {
  background: url(images/job-type.png) no-repeat 0 0;
  background-size: 13px 13px;
}
.job-details-info .company-info{
  line-height: 1;
  margin: 15px 0;
  overflow: hidden;
  padding: 0 15px;
}
.job-details-info .company-info > img{
  border: 1px solid #eee;
  border-radius: 3px;
  float: left;
  margin-right: 15px;
}
.job-details-info .company-info .company-intro{
  position: relative;
}
.job-details-info .company-info .company-intro::before,
.job-details-info .company-info .company-intro::after{
  border: 8px solid transparent;
  border-left-color: #9f9f9f;
  content: "";
  height: 0;
  position: absolute;
  right: -6px;
  top: 23px;
  width: 0;
}
.job-details-info .company-info .company-intro::after{
  border-left-color: #fff;
  right: -5px;
}
.job-details-info .company-intro .company-name{
  font-size: 15px;
  line-height: 1;
  margin-right: 14px;
}
.company-intro .company-name .name{
  vertical-align: middle;
}
.company-intro .company-name .vip{
  background: url(images/etp-vip.png) no-repeat 0 0;
  background-size: 36px 18px;
  display: inline-block;
  height: 18px;
  margin-left: 4px;
  vertical-align: middle;
  width: 36px;
}
.job-details-info .company-intro .company-area{
  color: #666;
  font-size: 14px;
  margin-right: 14px;
  padding-top: 15px;
}
.job-position{
  background-color: #fff;
  margin: 10px 0;
  padding-left: 15px;
}
.job-details .position-title{
  background: url(images/requirement.png) 0 10px no-repeat;
  background-size: 14px 14px;
  border-bottom: 1px solid #eee;
  line-height: 2;
  padding-left: 20px;
}
.job-details .content{
  background: url(images/job-details.png) 0 10px no-repeat;
  background-size: 14px 14px;
}
.job-position .position-item{
  font-size: 14px;
  line-height: 1.5;
  margin: 6px 0;
}
.job-position .position-item .item{
  color: #999;
}
.job-position .job-position-details{
  overflow: hidden;
  padding-right: 15px;
}
.job-details-content{
  background-color: #fff;
  margin-top: 10px;
  padding-left: 15px;
}
.job-details-content .position-content{
  margin-top: 10px;
  padding-bottom: 10px;
  padding-right: 15px;
  text-align: justify;
}
.job-fire{
  background-color: #fff;
  bottom: 0;
  -webkit-box-shadow: 0 0 5px 0 #eee;
          box-shadow: 0 0 5px 0 #eee;
  display: -ms-flexbox;
  display: flex;
  font-size: 18px;
  left: 0;
  position: fixed;
  right: 0;
}
.job-fire .remark{
  color: #666;
  font-size: 14px;
  padding: 0 30px;
}
.job-fire .remark .edit{
  background: url(images/remark-edit.png) center 0 no-repeat;
  background-size: 15px 14px;
  display: block;
  height: 16px;
  margin-top: 8px;
}
.job-fire .fire{
  background-color: #7cbf59;
  color: #fff;
  -ms-flex: 1 1;
      flex: 1 1;
  line-height: 50px;
  text-align: center;
}
.job-details .remark-area{
  background-color: #fff;
  padding-bottom: 58px;
}
div[contentEditable=true]:empty:not(:focus):before{
    color: #999;
    content:attr(data-text);
}
.remark-edit{
  background-color: #fff;
  border-top: 1px solid #d8d8d8;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  font-size: 15px;
  left: 0;
  line-height: 32px;
  position: fixed;
  right: 0;
  z-index: 1;
}
.remark-edit .remark-edit-fire{
  background-color: #f5f5f5;
  border-radius: 1em;
  color: #333;
  -ms-flex: 1 1;
      flex: 1 1;
  line-height: 30px;
  margin: 10px 15px;
  max-height: 74px;
  min-height: 30px;
  overflow: auto;
  padding: 0 15px;
  resize: none;
}
.remark-edit .remark-edit-fire .contents{
  line-height: 22px;
  margin: 4px 0;
}
.remark-edit .remark-edit-fire .contents:focus{
  outline: none;
}
.remark-edit .remark-fire{
  -ms-flex-item-align: center;
      align-self: center;
  color: #ccc;
  margin-right: 15px;
  text-decoration: none;
}
.remark-edit .remark-active{
  color: #7cbf59;
}



