.campus-details-module{
  background-color: #fff;
  height: 100%;
  overflow-y: auto;
}
.campus-details{
  background-color: #f2f2f2;
  font-size: 17px;
  overflow: hidden;
}
.campus-details .campus-info{
  background-color: #fff;
  margin-bottom: 10px;
  padding: 0 18px 20px;
}
.campus-details .campus-info .info-item{
  overflow: hidden;
}
.campus-details .campus-details-title{
  background-color: #fff;
  font-size: 18px;
  line-height: 1.5;
  padding: 10px 18px;
}
.campus-info .info-item .item-label{
  color: #7cbf59;
  float: left;
}
.campus-info .info-item .item-info{
  overflow: hidden;
  padding: 0 18px;
}
.campus-details-intro{
  background-color: #fff;
  overflow: hidden;
}
.campus-details-intro .campus-intro{
  border-left: 3px solid #7cbf59;
  line-height: 1;
  margin: 10px 0;
  text-indent: 18px;
}
.campus-details .campus-details-content{
  color: #666;
  margin-bottom: 10px;
  padding: 0 18px;
  text-align: justify;
}



