.article-module {
  background-color: #fff;
  margin-bottom: 8px;
  padding: 20px;
}
.article-module .article-title{
  border-left: 3px solid #52c168;
  font-size: 17px;
  line-height: 1;
  padding-left: 6px;
}
.article-module .article-close{
  background: url(images/close.png) no-repeat 0 0;
  background-size: 27px 21px;
  float: right;
  height: 21px;
  width: 27px;
}
.article-module > img{
  margin: 14px 0;
  vertical-align: top;
}
.article-info .info-name{
  color: #999;
  font-size: 12px;
}
.article-info .info-name .title{
  color: #333;
  font-size: 14px;
}
.article-info .info-name .time{
  float: right;
  margin-top: 1px;
}



